import React from "react";
import { Container, Typography, Button, Grid } from "@material-ui/core";
import {Link} from "react-router-dom"
import useStyles from "./styles";
import CartItem from "./CartItem/CartItem";
const Cart = ({ cart, handleUpdateCartQty, handleRemoveFromCart, handleEmptyCart }) => {
  const classes = useStyles();
  const EmptyCart = () => (
    <Typography variant="subtitle1">Your shopping cart is empty, start adding some products, Cheers!
    <Link to="/" className={classes.link}>Start adding something!</Link>
    </Typography>
  );

  const FilledCart = () => (
    <>
      <Grid container spacing={3}>
        {cart.line_items.map((lineItem) => (
          <Grid item key={lineItem.id} xs={12} sm={4}>
           <CartItem lineItem={lineItem} onUpdateCartQty={handleUpdateCartQty}  onRemoveFromCart={handleRemoveFromCart} />
          </Grid>
        ))}
      </Grid>
      <div className={classes.cartDetails}>
            <Typography variant="h4">Subtotal: {cart.subtotal.formatted_with_symbol}</Typography>
            <div>
                <Button className={classes.emptyButton} size="large" type="button" variant="contained" color="secondary" onClick={handleEmptyCart}>Empty Cart</Button>
                <Button component={Link} to="/checkout" className={classes.checkout} size="large" type="button" variant="contained" color="primary">Checkout</Button>
            </div>
      </div>
    </>
  );

  if(!cart.line_items) return 'Loading ....'
  return (
    <Container>
      <div className={classes.toolbar} />
      <Typography className={classes.title} variant="h3" gutterBottom>
        Your shopping cart
      </Typography>
      {!cart.line_items.length ? <EmptyCart /> : <FilledCart />}
    </Container>
  );
};

export default Cart;
