import React, {useState, useEffect} from "react";
import {Paper,Stepper,Step,Typography,StepLabel,CircularProgress,Divider,Button, CssBaseline} from "@material-ui/core";
import useStyles from "./styles";
import {Link, useNavigate } from "react-router-dom"
import AddressForm from "../AddressForm";
import PaymentForm from "../PaymentForm";

import { commerce } from "../../../lib/commerce";

const steps = ['Shipping Address', 'Payment details']
const Checkout = ({cart, order, onCaptureCheckout, error}) => {
  const classes = useStyles();
  const navigate = useNavigate ();
  const [activeStep, setActiveStep] = useState(0);
  const [isFinished, setisFinished] = useState(false)
  const [shippingData, setShippingData] = useState({})
  const [checkoutToken, setCheckoutToken] = useState(null)

  useEffect(()=>{
    const generateToken= async() =>{
        try {const token = await commerce.checkout.generateToken(cart.id, {type: 'cart'});
            setCheckoutToken(token);
        } catch (error) {navigate('/')}
    }
    generateToken();
  }, [cart]);

  const nextStep =()=> setActiveStep((prevActiveStep)=> prevActiveStep + 1);
  const backStep =()=> setActiveStep((prevActiveStep)=> prevActiveStep - 1);

  const next=(data)=>{
    setShippingData(data);
    nextStep();
  }

  const timeout =()=>{
    setTimeout(()=>{
      setisFinished(true);
    }, 3000)
  }

  let Confirmation = () => order.customer?(
    <>
      <div>
        <Typography variant="h5">Thank you for your purchase {order.customer.firstame} {order.customer.lastame}</Typography>
        <Divider className={classes.divider} />
        <Typography variant="subtitle2">Order reference: {order.customer.reference}</Typography>
      </div>
      <br />
      <Button component={Link} to="/" variant="outlined" type="button">Back to home</Button>
    </>
  ) : isFinished? (
    <div>
        <Typography variant="h5">Thank you for your purchase </Typography>
        <Divider className={classes.divider} />
        <Typography variant="subtitle2">Order reference: ref</Typography>
      </div>
  ): (
    <div className={classes.spinner}>
      <CircularProgress />
    </div>
  );

  if (error){
    <>
    <Typography variant="h5">
    Error: {error}
    <br />
      <Button component={Link} to="/" variant="outlined" type="button">Back to home</Button>
    </Typography>
    </>
  }

  const Form=()=> activeStep===0?
  <AddressForm checkoutToken={checkoutToken} next={next} /> : 
  <PaymentForm timeout={timeout} nextStep={nextStep} shippingData={shippingData} checkoutToken={checkoutToken} backStep={backStep} onCaptureCheckout={onCaptureCheckout}/>

  return (
    <>
    <CssBaseline />
      <div className={classes.toolbar} />
      <main className={classes.layout}>
        <Paper className={classes.paper}>
          <Typography variant="h4" align="center">
            Checkout
          </Typography>
          <Stepper activeStep={activeStep} className={classes.stepper}>
            {steps.map((step) =>(
                <Step key={step}>
                    <StepLabel>{step}</StepLabel>
                </Step>
            ))}
          </Stepper>
          {activeStep === steps.length ? <Confirmation /> : checkoutToken && <Form />}
        </Paper>
      </main>
    </>
  );
};

export default Checkout;
